.popup {
  display: flex;
  max-width: 100%;
  max-height: 100%;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s, opacity .3s;
}
    
 
